import {ReactSVG} from "react-svg";
import {useSelector} from "react-redux";
import iconMyLevel from "assets/icons/iconMyLevel.svg";
import iconMyLevelSmall from "assets/icons/iconMyLevelSmall.svg";
import iconMyLevelSmall2 from "assets/icons/iconMyLevelSmall2.svg";
import iconCheckMyLevel from "assets/icons/iconCheckMyLevel.svg";
import iconInfoBig from "assets/icons/iconInfoBig.svg";
import {AppStateType} from "core/redux/store";
import {MyLevelCardBody} from "../myLevelCardBody/myLevelCardBody";
import style from "../../structure.module.css";
import iconInfo from 'assets/icons/iconInfo.svg'

export const MyLevelCard = (): JSX.Element => {
    const {
        currentLevel, currentFirstLine, currentStructureBalance, nextStructureBalance,
        nextBalance, currentBalance
    } = useSelector((state: AppStateType) => state.nextLevelBlock.nextLevelBlock)

    console.log(currentFirstLine)

    return (
        <div className={style.myLevelBlock}>
            <div className={style.myLevelBlock_top}>
                <div className={style.myLevelStatus}>
                    <ReactSVG className={style.iconMyLevel} src={iconMyLevel}/>
                    <span>{currentLevel}</span>
                </div>
                <div className={style.aboutMy}>
                    <span>Мой уровень</span>
                    <h3>lvl {currentLevel} — Новичек</h3>
                </div>
            </div>
            <div className={style.myLevelBlock_body}>
                <div className={style.myLevelBlock_body_top}>
                    <ReactSVG className={style.iconInfo} src={iconInfoBig}/>
                    <span>Для повышения уровня <br/> выполните условия</span>
                </div>
                <div className={style.myLevelBlock_body_card}>
                    <MyLevelCardBody currentValue={nextBalance}
                                     widthBlock={(currentBalance * 100) / nextBalance}
                                     subTitle={currentBalance}>
                         <span className={style.title}>{'Инвестируйте'}
                             <ReactSVG className={style.iconInfo} src={iconInfo}/>
                         </span>
                    </MyLevelCardBody>

                    <MyLevelCardBody currentValue={nextStructureBalance}
                                     widthBlock={(currentStructureBalance * 100) / nextStructureBalance}
                                     subTitle={currentStructureBalance}>
                        <span className={style.title}>{'Обьем в баллах'}
                            <ReactSVG className={style.iconInfo} src={iconInfo}/>
                         </span>
                    </MyLevelCardBody>

                    <MyLevelCardBody currentValue={'1'} widthBlock={70} subTitle={<StatisticLevel/>}>
                         <span className={style.title}>{'Первая линия'}
                             <ReactSVG className={style.iconInfo} src={iconInfo}/>
                         </span>
                    </MyLevelCardBody>

                    <MyLevelCardBody currentValue={<ReactSVG src={iconCheckMyLevel}/>} widthBlock={100}
                                     subTitle={'Партнеров на 2 линии'}>
                         <span className={style.title}>{'Ветки'}
                             <ReactSVG className={style.iconInfo} src={iconInfo}/>
                         </span>
                    </MyLevelCardBody>
                </div>
            </div>
        </div>
    )
}

const StatisticLevel = ({value1 = 2, value2 = 1}): JSX.Element => {

    return (
        <div className={style.statisticLevel}>
            <div className={style.statisticLevel_leftBlock}>
                <div className={style.currentLevel}>
                    <ReactSVG className={style.iconMyLevelSmall} src={iconMyLevelSmall}/>
                    <span className={style.level}>1</span>
                </div>
                <span>{value1} чел.</span>
            </div>
            <div className={style.statisticLevel_rightBlock}>
                <div className={style.currentLevel}>
                    <ReactSVG className={style.iconMyLevelSmall} src={iconMyLevelSmall2}/>
                    <span className={style.level}>4</span>
                </div>
                <span>{value2} чел.</span>
            </div>
        </div>
    )
}