import {useSelector} from "react-redux";
import {TopBlock} from 'ui/topBlock/topBlock'
import style from './recovery.module.css'
import {ForgotPassword} from "./forgotPassword/forgotPassword";
import {AppStateType} from "core/redux/store";
import {NewPassword} from "./newPassword/newPassword";

export const Recovery = (): JSX.Element => {
    const {success} = useSelector((state: AppStateType) => state.resetPassword)


    return (
        <div className={style.recoveryWrapper}>
            <div className={style.recovery}>
                <TopBlock/>
                {success ? (<NewPassword/>) : (<ForgotPassword/>)}
            </div>
        </div>
    )
}