import {useForm} from "react-hook-form";
import {useState} from "react";
import {ErrorsMessage} from "ui/errorsMessage/errorsMessage";
import {ButtonEnter} from "ui/buttons/buttonEnter/buttonEnter";
import {InputPassword} from "ui/inputs/inputPassword/inputPassword";
import style from '../../recovery.module.css'
import {ReactSVG} from "react-svg";
import iconBtnEnter from "assets/icons/iconBtnEnter.svg";


interface Props {
    onSubmitHandle: () => void
}

interface FormProps {
    password: string,
    repeatPassword: string
}

export const NewPasswordForm = ({onSubmitHandle}: Props): JSX.Element => {
    const [isMatchingPasswords, setIsMatchingPasswords] = useState(false)
    const {handleSubmit, control, formState: {errors}} = useForm<FormProps>({
        defaultValues: {
            password: '',
            repeatPassword: '',
        },
        mode: 'onChange',
    });

    const onSubmit = ({repeatPassword, password}: FormProps) => {
        if (password === repeatPassword) {
            console.log(password)
        } else {
            setIsMatchingPasswords(prev => !prev)
        }
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <div className={style.formItem}>
                <label htmlFor="password">Новый пароль</label>
                <InputPassword control={control} name={'password'} rules={{required: true}}/>
                {errors.password && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}
            </div>

            <div className={style.formItem}>
                <label htmlFor="repeatPassword">Подтвердите новый пароль</label>
                <InputPassword control={control} name={'repeatPassword'} rules={{required: true}}/>
                {errors.repeatPassword && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}
            </div>
            {isMatchingPasswords && (<ErrorsMessage textMessage={'Пароли не совпадают!'}/>)}

            <ButtonEnter> Сменить пароль</ButtonEnter>

            <div className={style.footerForm}>
                <span className={style.title}>Уже есть аккаунт?</span>
                <span className={style.subTitle}>
                    <ReactSVG src={iconBtnEnter} />
                    <span className={style.subTitle}>Войти</span>
                </span>
            </div>
        </form>
    )
}