import {combineReducers} from "@reduxjs/toolkit";
import {articlesReducer} from 'modules/dashboard/articles/articlesSlice';
import {partnersReducer} from 'modules/dashboard/partners/partnersSlice';
import {getUserDataReducer} from 'modules/user/getUserData/getUserDataSlice';
import {contractReducer} from 'modules/contracts/ContractsSlice';
import {buyContractReducer} from 'modules/contracts/buyContracts/buyContractsSlice';
import {registrationReducer} from "modules/registration/registrationSlice";
import {partnersSiteReducer} from 'modules/partnersSite/partnersSiteSlice';
import {oneNewsReducer} from 'modules/news/oneNewsSlice';
import {eventsReducer} from 'modules/calendar/events/eventsSlice';
import {calendarDayReducer} from 'modules/calendar/calendarDay/celandarSlice';
import {structurePartners} from "modules/structure/partners/structurePartnersSlice";
import {structureLinesReducer} from "modules/structure/lines/structureLinesSlice";
import {structureBonusesReducer} from "modules/structure/bonuses/structureBonusesSlice";
import {bonusesTransactionsReducer} from "modules/structure/bonusesTransactions/bonusesTransactionsSlice";
import {nextLevelBlockSliceReducer} from "modules/structure/nextLevelBlock/nextLevelBlockSlice";
import {structurePartnersReducer} from "modules/structure/partnersStatistic/partnersStatisticSlice";
import {transactionsReducer} from 'modules/history/transactionsSlice';
import {walletTransactionsReducer} from 'modules/history/wallet/walletTransactionsSlice';
import {promoListReducer} from 'modules/promo/promoListSlice';
import {getInheritancesListReducer} from "modules/user/getInheritancesList/getInheritancesListSlice";
import {rejectContractReducer} from 'modules/contracts/rejectContract/rejectContractSlice';
import {sendSmsCodeReducer} from "modules/registration/sendSmsCode/sendSmsCodeSlice";
import {checkSmsCodeReducer} from "modules/registration/checkSmsCode/checkSmsCodeSlice";
import {resetPasswordReducer} from "modules/recoveryPassword/resetPassword/resetPasswordSlice";
import {authorizationReducer} from "modules/authorization/authorizationSlice";
import {contractValueInfoReducer} from "modules/contracts/valueInfo/contractValueInfoThunkSlice";


export const rootReducer = combineReducers({
    authorization: authorizationReducer,

    registration: registrationReducer,
    sendSmsCode: sendSmsCodeReducer,
    checkSmsCode: checkSmsCodeReducer,
    resetPassword: resetPasswordReducer,

    articles: articlesReducer,
    partners: partnersReducer,

    user: getUserDataReducer,

    contracts: contractReducer,
    buyContracts: buyContractReducer,
    partnersSites: partnersSiteReducer,
    oneNews: oneNewsReducer,
    events: eventsReducer,
    calendarDay: calendarDayReducer,

    structureLines: structureLinesReducer,
    structurePartners: structurePartners,
    structureBonuses: structureBonusesReducer,
    structureBonusesTransactions: bonusesTransactionsReducer,
    nextLevelBlock: nextLevelBlockSliceReducer,
    structurePartnersStatistic: structurePartnersReducer,

    transactions: transactionsReducer,
    walletTransactions: walletTransactionsReducer,
    promoList: promoListReducer,
    getInheritancesList: getInheritancesListReducer,
    rejectContract: rejectContractReducer,
    contractValueInfo: contractValueInfoReducer,
})
