import {FC, ReactNode} from "react";
import style from './myLevelCardBody.module.css'

interface Props {
    widthBlock: number,
    subTitle: string | ReactNode,
    currentValue: string | ReactNode,
}

export const MyLevelCardBody: FC<Props> = (props): JSX.Element => {
    const {widthBlock, subTitle, currentValue} = props

    return (
        <div className={style.myLevelCardBody}>
            <div style={{width: `${widthBlock}%`}}
                 className={ `${style.myLevelCardBody_leftBlock} ${widthBlock === 100 ? style.border : ''}`}>
                {props.children}
                <span className={style.subTitle}>{subTitle}</span>
            </div>

            <div className={style.myLevelCardBody_rightBlock}>
                <span>{currentValue}</span>
            </div>
        </div>
    )
}