import {ForgotPasswordForm} from "./component/forgotPasswordForm";
import style from '../recovery.module.css'
import {RepeatSmsSend} from "ui/repeatSmsSend/repeatSmsSend";
import {useDispatch} from "react-redux";
import {Redirect} from "react-router-dom";
import {resetPassword} from "modules/recoveryPassword/resetPassword/resetPasswordThunk";
import {ArrowBack} from "ui/buttons/arrowBack/arrowBack";
import {ReactSVG} from "react-svg";
import iconPhone from 'assets/icons/iconPhone.svg'

export const ForgotPassword = (): JSX.Element => {
    const dispatch = useDispatch()

    const onSubmitHandle = (email: string) => {
        dispatch(resetPassword(email))
    }

    const redirectToBack = () => <Redirect to="login"/>

    return (
        <div className={style.forgotPassword}>
            <ArrowBack linkToBack={'login'} onClick={() => redirectToBack}/>

            <h1>Забыл пароль</h1>
            <p>Введите ваш email, мы отправим вам сообщение с инструкцией по восстановлении</p>

            <ForgotPasswordForm onSubmitHandle={onSubmitHandle}/>
            <RepeatSmsSend/>

            <div className={style.footerForm}>
                <span className={style.title}>Не помните свой email?</span>
                <span className={style.subTitle}> <ReactSVG src={iconPhone}/>
                    <span>Восстановится через телефон</span>
                </span>
            </div>
        </div>
    )
}