import {createSlice} from '@reduxjs/toolkit';
import {resetPassword} from './resetPasswordThunk';

const resetPasswordSlice = createSlice({
    name: 'resetPassword',
    initialState: {
        error: false,
        loading: false,
        success: false,
        errorsMessage: ''
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(resetPassword.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(resetPassword.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success.status
                state.loading = false;
            }
        });
        builder.addCase(resetPassword.rejected, (state, action) => {
            state.loading = false;
            state.error = true;
            // @ts-ignore
            state.errorsMessage = action.payload.response.data.message
        });
    },

});

export const resetPasswordReducer = resetPasswordSlice.reducer;