import {useEffect} from "react";
import {ReactSVG} from "react-svg";
import {NavLink, Redirect} from "react-router-dom";
import {AppStateType} from "core/redux/store";
import {useDispatch, useSelector} from "react-redux";
import {TopBlock} from "ui/topBlock/topBlock"
import iconQuestions from "assets/icons/iconQuestions.svg";
import iconRegistration from "assets/icons/iconRegistration.svg";
import {authorizations, createClient} from "modules/authorization/authorizationThunk";
import { AuthorizationData, AuthorizationRequest } from "api/type/authorisation";
import { AuthorizationForm } from "./components/authorizationForm";
import {SliderLazyLoad} from "./components/slider/slidert";
import style from './authorization.module.css';

export const Authorization = (): JSX.Element => {
    const {error, isAuth, grant_type} = useSelector((state: AppStateType) => state.authorization.authorization);
    const {client_id, client_secret} = useSelector((state: AppStateType) => state.authorization.createClient);
    const dispatch = useDispatch()


    useEffect(() => {
        dispatch(createClient())
    }, [dispatch])

    if (isAuth) {
        return <Redirect to={`/dashboard`}/>
    }

    const onSubmit = ({username, password}: AuthorizationData) => {
        const requestData: AuthorizationRequest = {username, password, client_id, client_secret, grant_type}

        dispatch(authorizations(requestData))
    }

    return (
        <div className={style.authorization}>
            <div className={style.slider}>
                <SliderLazyLoad/>
            </div>

            <div className={style.authorizationForm}>
                <TopBlock/>

                <div>
                    <div className={style.authorizationFormWrapper}>
                        <h1>С возвращением!</h1>
                        <span>Войти на платформу</span>
                    </div>
                    <AuthorizationForm error={error} onSubmitHandle={onSubmit}/>
                </div>
                <div className={style.authorizationFooter}>
                    <div className={style.authorizationFooter_rightBlock}>
                        <span>У вас еще нет аккаунта?</span>
                        <NavLink to={'/registration'}>
                            <ReactSVG className={style.icon} src={iconRegistration}/>
                            Зарегистрироваться</NavLink >
                    </div>
                    <ReactSVG className={style.iconQuestions} src={iconQuestions}/>
                </div>
            </div>
        </div>
    )
}