import {useSelector} from "react-redux";
import {useForm} from "react-hook-form";
import {Input} from "ui/inputs/input/input"
import {ErrorsMessage} from "ui/errorsMessage/errorsMessage";
import {ButtonEnter} from "ui/buttons/buttonEnter/buttonEnter";
import style from '../../recovery.module.css';
import {AppStateType} from "core/redux/store";

interface Props {
    onSubmitHandle: (email: string) => void,
}

export const ForgotPasswordForm = ({onSubmitHandle}: Props): JSX.Element => {
    const {errorsMessage} = useSelector((state: AppStateType) => state.resetPassword)
    const {handleSubmit, control, formState: {errors}} = useForm<{ email: string }>({
        defaultValues: {
            email: '',
        },
        mode: 'onChange',
    });

    const onSubmit = ({email}: { email: string }) => {
        onSubmitHandle(email)
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <div className={style.formItem}>
                {errorsMessage === 'User not found' ?
                    (<ErrorsMessage textMessage={'Такой пользовательно не существует'}/>) :
                    (<label htmlFor="email">E-mail</label>)}
                <Input control={control} name='email' rules={{required: true}}/>
                {errors.email && (<ErrorsMessage textMessage={'Это поле обязательно'}/>)}
            </div>
            <ButtonEnter> Восстанвить пароль</ButtonEnter>
        </form>
    )
}