import {createSlice} from '@reduxjs/toolkit';
import {contractValueInfoThunk} from './contractValueInfoThunk';

const contractValueInfoSlice = createSlice({
    name: 'contractValueInfo',
    initialState: {
        contract: 0,
        transaction: 0,
        rzmTransaction: 0,
        loading: false,
        error: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(contractValueInfoThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(contractValueInfoThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.contract = action.payload.contract
                state.rzmTransaction = action.payload.rzmTransaction
                state.transaction = action.payload.transaction
                state.loading = false
            }
        });
        builder.addCase(contractValueInfoThunk.rejected, (state) => {
            state.loading = false
            state.error = true
        });
    },

});

export const contractValueInfoReducer = contractValueInfoSlice.reducer;