import style from "./repeatSmsSend.module.scss";
import {ReactSVG} from "react-svg";
import iconRepeatSmsSend from "../../assets/icons/iconRepeatSmsSend.svg";

interface Props {
    onClick?: () => void
}

export const RepeatSmsSend = ({onClick}: Props): JSX.Element => {
    return (
        <button onClick={onClick} className={style.repeatSmsSend}>
            <ReactSVG src={iconRepeatSmsSend} className={style.iconRepeatSmsSend}/>Отправить еще раз
        </button>
    )
}
